import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { Job } from '../models/job';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class JobsService {

  httpOpions = {
    headers: new HttpHeaders({'Authorization':'Bearer '+environment.token})
  }
  atrCtrl: any;
  
  constructor(private httpClient: HttpClient) { }

  
  addJob(job: Job, token:string): Observable<Job>{
    this.httpOpions = {
      headers: new HttpHeaders({'Authorization':'Bearer '+token})
    }
    //this.showConfirm('invocando a api');
      console.log(JSON.stringify(job))
       return this.httpClient.post<Job>(environment.apiJobs,job,this.httpOpions)
       .pipe(
         retry(2),
         catchError(this.handleError)
       )
     }
 
   // Manipulação de erros
   handleError(error: HttpErrorResponse) {
     let errorMessage = '';
     if (error.error instanceof ErrorEvent) {
       // Erro ocorreu no lado do client
       errorMessage = error.error.message;
     } else {
       // Erro ocorreu no lado do servidor
       errorMessage = `Código do erro: ${error.status}, ` + `menssagem: ${error.message}`;
     }
     //this.showConfirm(errorMessage);
     return throwError(errorMessage);
   }
 }
