import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Job } from '../models/job';
import { JobsService } from '../services/jobs.service';
import { LocalstorageService } from '../services/localstorage.service';

@Component({
  selector: 'app-job-reg',
  templateUrl: './job-reg.page.html',
  styleUrls: ['./job-reg.page.scss'],
})
export class JobRegPage implements OnInit {

  jobForm: FormGroup;

  objJob = {} as Job;

  idUserCompany:any;
  userToken    : any;

  
  constructor(private jobservice:JobsService, private localstorage: LocalstorageService) { }

  async jobPOST(oJob:any, token:string){
    await this.jobservice.addJob(oJob, token).subscribe((ret)=>{
      console.log(ret);
    });   ;
  }

  ngOnInit() {
    this.jobForm = new FormGroup({
      jobtitle : new FormControl(),//'', Validators.compose([Validators.required, CompanyValidator.isValid])),
      jobDescription : new FormControl()
    });
    this.getData();
  }

  async getData(){
    this.idUserCompany = await this.localstorage.getValue('compId'); 
    this.userToken     = await this.localstorage.getValue('token');
  }

  submitForm(){
    this.objJob.company = this.idUserCompany;
    this.objJob.jobTitle = this.jobForm.get('jobtitle').value;
    this.objJob.jobDescription = this.jobForm.get('jobDescription').value;
    //this.postFeed.postDate = formatDate()  date;
    console.log('olha o token'+this.userToken);
    console.log('Olha a empresa'+this.idUserCompany);
    this.jobPOST(this.objJob, this.userToken);
    //console.log('Pelo menos clicou');
  }

}
